<template>
  <div class="app-container">
    <table class="table" width="100%" border="1" collspacing="0" cellpadding="0">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>appId</td>
          <td></td>
          <td>String</td>
          <td></td>
        </tr>
        <tr>
          <td>type</td>
          <td></td>
          <td>String</td>
          <td> basic basic</td>
        </tr>
        <tr>
          <td>style</td>
          <td></td>
          <td>String</td>
          <td> embedinlinepopuponeclick <a href="https://www.dingxiang-inc.com/business/captcha-online">demo</a></td>
        </tr>
        <tr>
          <td>inlineFloatPosition</td>
          <td></td>
          <td>String</td>
          <td> style  inline  updown demo</td>
        </tr>
        <tr>
          <td>oneClickFloatPosition</td>
          <td></td>
          <td>String</td>
          <td> style  oneclick  upstyleoneclick</td>
        </tr>
        <tr>
          <td>width</td>
          <td></td>
          <td>Number</td>
          <td> 300</td>
        </tr>
        <tr>
          <td>language</td>
          <td></td>
          <td>String</td>
          <td> cnen</td>
        </tr>
        <tr>
          <td>success</td>
          <td></td>
          <td>Function</td>
          <td> token:constId</td>
        </tr>
        <tr>
          <td>fail</td>
          <td></td>
          <td>Function</td>
          <td></td>
        </tr>
      </tbody>
    </table>
    <div>remote-js</div>
    <remote-js :src="'https://cdn.dingxiang-inc.com/ctu-group/captcha-ui/index.js'" :loaded="remoteJsLoaded"></remote-js>
    <div>
      <p></p>
      <div class="style-item">
        
        <button class="btn" @click="styleChange('embed')">embed</button>
        <button class="btn" @click="styleChange('inline')">inline</button>
        <button class="btn" @click="styleChange('popup')">popup</button>
        <button class="btn" @click="styleChange('oneclick')">oneclick</button>
      </div>
      <div class="style-item">
        
        <button class="btn" @click="widthChange(200)">200</button>
        <button class="btn" @click="widthChange(300)">300</button>
        <button class="btn" @click="widthChange(400)">400</button>
        <button class="btn" @click="widthChange(600)">600</button>
      </div>
    </div>
    <div class="style-item">
      :
      <button class="btn" @click="inlineFloatPositionChange('up')">up</button>
      <button class="btn" @click="inlineFloatPositionChange('down')">dowm</button>
    </div>
    <div class="style-item">
      :
      <button class="btn" @click="languageChange('cn')"></button>
      <button class="btn" @click="languageChange('en')"></button>
    </div>
    <div class="style-item">
      :
      <input type="color" @change="bgColorChange">
    </div>
    <div class="style-item">
      :
      <button class="btn" @click="reload"></button>
      <button class="btn" @click="show"></button>
      <button class="btn" @click="hide"></button>
    </div>
    <div id="captcha" class="captcha"></div>
  </div>
</template>

<script>
import RemoteJs from '../../components/RemoteJs/index'
export default {
  components: { RemoteJs },
  data() {
    return {
      myCaptcha: null,
      style: 'embed',
      inlineFloatPosition: 'up',
      oneClickFloatPosition: 'up',
      width: 300,
      language: 'cn',
      bgColor: 'transparent'
    }
  },
  methods: {
    remoteJsLoaded() {
      this.captcha()
    },
    show() {
      this.myCaptcha.show()
    },
    hide() {
      this.myCaptcha.hide()
    },
    reload() {
      this.myCaptcha.reload()
    },
    bgColorChange(e) {
      this.bgColor = e.target.value
      this.captcha()
    },
    widthChange(width) {
      this.width = width
      this.captcha()
    },
    styleChange(style) {
      this.style = style
      this.captcha()
    },
    inlineFloatPositionChange(inlineFloatPosition) {
      this.inlineFloatPosition = inlineFloatPosition
      this.captcha()
    },
    languageChange(language) {
      this.language = language
      this.captcha()
    },
    captcha() {
      const { style, inlineFloatPosition, oneClickFloatPosition, width, language, bgColor } = this
      const _dx = window._dx || {}
      this.myCaptcha = _dx.Captcha(document.getElementById('captcha'), {
        appId: '3657937611284dbc1d1f13458ce920b5',
        style,
        inlineFloatPosition,
        oneClickFloatPosition,
        width,
        language,
        customStyle: {
          bgColor
        },
        success: (token) => {
          console.log({style, token})
        },
        fail: (err) => {
          console.log(err)
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.table {
  border-spacing: 0;
  border-collapse: collapse;
}
.style-item {
  margin-bottom: 16px;
}
.btn {
  width: 80px;
  line-height: 24px;
  border: 1px solid #eaeaea;
  border-radius: 4px;
  background-color: #f8f8f8;
  cursor: pointer;
}
</style>
